/* ------------------------------------------------------ */
/* -- Header -- */  
  .h-left{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .h-left-wrapper{
    padding:40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .h-intro{
    font-size:30px;
    font-weight:300;
  }
  .h-name{
    font-size: 60px;
    margin-top: 1rem;
  }
  .h-title{
    height: 50px;
    overflow: hidden;
    margin-top: 1rem;
  }
  .h-title-item{
    height:50px;
    font-size:30px;
    font-weight: bold;
    color: #5B63B7;
    display: flex;
    align-items: center;
    margin-top: 1rem;
  }
  .h-title-wrapper{
    height: 60%;
    animation: move 10s ease-in-out infinite alternate;
  }
.btn-primary{
    margin-top: 1.5rem;
  }
  
  @keyframes move{
    33%{
      transform:translateY(-50px);
    }
    66%{
      transform: translateY(-100px);
    }
    99%{
      transform: translateY(-150px);
    }
    
  }
  
  @media screen and (max-width: 1024px){
    .works-container{
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }
}

@media screen and (max-width: 600px){
    .works-container{
        grid-template-columns: 1fr;
        gap: 1rem;
    }
}