.exp-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.exp-container > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transistion);
}

.exp-container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.exp-container > div h3{
    text-align: center;
    color: var(--color-primary);
    margin-bottom: 2rem;
}

.exp-content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.exp-details{
    display: flex;
    gap: 1rem;
}

.exp-icons{
    margin-top: 6px;
    color: var(--color-primary)
}


@media screen and (max-width: 1024px){
    .exp-container{
        grid-template-columns: 1fr;
    }
    .exp-container > div{
       width: 80%;
       padding: 2rem;
       margin: 0 auto;
    }

    .exp-details{
        padding: 1rem;
    }
}

@media screen and (max-width: 600px){
    .exp-container{
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .exp-container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
}


